<template>
	<view class="wrapper">
		<view class="padding current_city">
			<!-- 字母 -->
			<view class="flex-row-start zimu">
				<text class="doit"></text>
				<text>当前城市</text>
			</view>
			<!-- item -->
			<view class="cu-list flex-row-between padd">
				<view class="city_name" @click="selectCurrentCity(myPosition)">{{myPosition.province}}</view>
				<view class="position flex-row-start" @click="getLocation">
					<image src='../../static/location.png'></image>
					<text>重新定位</text>
				</view>
			</view>
		</view>
		
		<scroll-view 
			scroll-y
			class="indexes" 
			:scroll-into-view="'indexes-'+ listCurID"
			:scroll-with-animation="true" 
			:enable-back-to-top="true"
		>	
			<block v-for="(item,index) in list" :key="index">
				<view :class="'indexItem-' + item.indx+' padding'" :id="'indexes-' + item.name" :data-index="item.name">
					<!-- 字母 -->
					<view class="flex-row-start zimu">
						<text class="doit"></text>
						<text>{{item.name}}</text>
					</view>
					<!-- item -->
					<view class="cu-list no-padding">
						<view class="cu-item" v-for="(lett,idx) in item.list" :key="idx" @click="selectCity(lett)">
							<view class="txt">{{lett.name}}</view>
						</view>
					</view>
				</view>
			</block>
			
		</scroll-view>
		<!-- 索引兰 -->
		<view class="indexBar">
			<view 
				class="indexBar-box" 
				@touchstart="tStart" 
				@touchend="tEnd" 
				@touchmove.stop="tMove"
			>	
				<view 
					class="indexBar-item" 
					v-for="(item,index) in list" 
					:key="index" 
					:id="index" 
					@touchstart="getCur" 
					@touchend="setCur"
				> 
					{{item.name}}
				</view>
			</view>
		</view>
		<!--选择显示-->
		<view v-show="!hidden" class="indexToast">
			{{listCur}}
		</view>
	</view>
</template>

<script>
	import amapFile from 'common/amap-wx.js'
	import {Config} from "../../common/Const.js"
	export default {
		data() {
			return {
				hidden: true,
				listCurID: '',// 当前id
				list:[], // list 
				listCur: '', //当前值 ABCS
			};
		},
		computed:{
			myPosition(){
				return this.$store.state.addrInfo
			}
		},
		onLoad() {
			this.getCityList()
		},
		onReady() {
			let that = this;
			uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
				that.boxTop = res.top
			}).exec();
			uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
				that.barTop = res.top
			}).exec()
		},
		methods: {
			//获取文字信息
			getCur(e) {
				this.hidden = false;
				this.listCur = this.list[e.target.id].name;
			},
			setCur(e) {
				this.hidden = true;
				this.listCur = this.listCur
			},
			//滑动选择Item
			tMove(e) {
				let y = e.touches[0].clientY,
					offsettop = this.boxTop,
					that = this;
				//判断选择区域,只有在选择区才会生效
				if (y > offsettop) {
					let num = parseInt((y - offsettop) / 20);
					this.listCur = that.list[num].name
				};
			},

			//触发全部开始选择
			tStart() {
				this.hidden = false
			},

			//触发结束选择
			tEnd() {
				this.hidden = true;
				this.listCurID = this.listCur
			},
			indexSelect(e) {
				let that = this;
				let barHeight = this.barHeight;
				let list = this.list;
				let scrollY = Math.ceil(list.length * e.detail.y / barHeight);
				for (let i = 0; i < list.length; i++) {
					if (scrollY < i + 1) {
						that.listCur = list[i].index;
						that.movableY = i * 20
						return false
					}
				}
			},
			getCityList(){
				this.request("/store/city").then((res)=>{
					if(res.resultCode==0){
						var _data=res.data
						var _list=[]
						var _keys=Object.keys(_data)
						for(var i=0;i<_keys.length;i++){
							_list.push({
								name:_keys[i],
								list:_data[_keys[i]]
							})
						}
						this.list=_list
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			getLocation() {
				// uni.showLoading()
				uni.getLocation({
					type: 'gcj02',
					success: (res) => {
						var myAmapFun = new amapFile.AMapWX({
							key: Config.mapKey
						});
						myAmapFun.getRegeo({
							location: res.longitude + "," + res.latitude,
							success:(data)=>{
								uni.showToast({
									title:"定位成功",
									icon:"success"
								})
								let _obj = {
									lonlat: [res.longitude, res.latitude],
									name:data[0].desc,
									adress:data[0].regeocodeData.formatted_address,
									province:data[0].regeocodeData.addressComponent.province,
									cityCode:data[0].regeocodeData.addressComponent.adcode
								}
								// 默认把当前城市选为 选择的城市
								this.$store.commit("SET_ADDRESS", _obj)
							},
							fail:(err)=>{
								uni.showToast({
									title:"定位失败"
								})
							}
						})
					}
				})
			},
			selectCity(item){
				let pages=getCurrentPages()
				// 上一页
				var prePage=pages[pages.length-2]
				if(prePage){
					prePage.$vm.selectCityInfo=item
				}
				uni.navigateBack({
					delta:1
				})
			},
			selectCurrentCity(params){
				/* selectCityInfo:{
					centralPoint:null, //"116.405285,39.904989"
					code:null,// "110100"
					name: null,// "110100"
				}, */
				var _obj={
					centralPoint:params.lonlat.join(","),
					code:params.cityCode,
					name:params.province
				}
				this.selectCity(_obj)
			}
		}
	}
</script>
<style lang="stylus">
	.wrapper
		.current_city
			box-shadow:0px -2px 20px 0px rgba(37,90,209,0.1);
			.padd
				.city_name
					height:72upx;
					padding:0upx 50upx
					line-height 72upx
					background:rgba(243,246,251,1);
					border-radius:8upx;
					font-size:26upx;
					font-family:PingFangSC-Medium,PingFang SC;
					font-weight:500;
					color:rgba(31,35,52,1);
				.position
					image
						width:28upx
						height:28upx
						margin-right:10upx
		.zimu
			color:#1F2334
			.doit
				width:8upx;
				height:8upx;
				background:rgba(53,183,163,1);
				border-radius:2upx;
				margin-right:10upx
		.cu-list
			margin-top:20upx
			.cu-item
				height:100upx
				padding-left:20upx
				line-height 100upx
				border-bottom:2upx solid #F9FBFC
				&:last-child
					border:none
	
</style>
<style>
	.indexes {
		position: relative;
		height:84vh;
	}

	.indexBar {
		position: fixed;
		right: 0px;
		top: 192upx;
		padding: 20upx 20upx 20upx 60upx;
		display: flex;
		align-items: center;
	}

	.indexBar .indexBar-box {
		width: 40upx;
		height: auto;
		display: flex;
		flex-direction: column;
		/* box-shadow: 0 0 20upx rgba(0, 0, 0, 0.1); */
		border-radius: 10upx;
	}

	.indexBar-item {
		width: 40upx;
		height:40upx;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 24upx;
		color:#35B7A3;
		font-family:PingFangSC-Regular,PingFang SC;
		font-weight:400;
	}

	.indexToast {
		position: fixed;
		top: 0;
		right: 80upx;
		bottom: 0;
		background: rgba(0, 0, 0, 0.5);
		width: 100upx;
		height: 100upx;
		border-radius: 10upx;
		margin: auto;
		color: #fff;
		line-height: 100upx;
		text-align: center;
		font-size: 48upx;
	}
</style>
